.bottom-selector{width:100%;display:flex;justify-content:space-around;justify-self:flex-end;flex-grow:1;align-items:flex-end;font-size:.8em;font-family:"Antic Slab",sans-serif;padding-bottom:2.5rem}.bottom-selector p{padding:.2rem .5rem;margin:.8rem;border-radius:5px}.bottom-selector-text{width:50%;display:flex;flex-direction:column;align-items:center;opacity:.7;transition:.5s}.bottom-selector-text.selected{font-size:.9em;opacity:1}.bottom-selector-text.selected p{border:solid 1px}
.top{width:100%;height:100vh;overflow:hidden;display:flex;flex-direction:column;padding-top:15vh;box-sizing:border-box;mix-blend-mode:difference;font-size:min(3em,3vw)}.name{font-size:4em;text-align:left;font-family:"Unica One",cursive;margin:0 15vh}.title{font-size:1em;padding-right:5em;text-align:end;white-space:nowrap;font-family:"Antic Slab",sans-serif;color:#ffc1c1}.show-artworks{position:absolute;bottom:5vh;display:flex;flex-direction:column;align-items:center;width:100%}.show-artworks p{font-size:2vw;font-family:"Antic Slab",sans-serif;margin:0;margin-bottom:1vh;transition:.15s}.show-artworks p:hover{-webkit-transform:scale(1.25);transform:scale(1.25)}
.main-cursor,.secondary-cursor{z-index:10000;pointer-events:none;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);position:fixed}.main-cursor{transition:opacity 1s cubic-bezier(0.77, 0, 0.175, 1);-webkit-animation:fadeIn 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:fadeIn 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;mix-blend-mode:difference}.main-cursor .main-cursor-background{width:30px;height:30px;background:#dbbaba;border-radius:50%}.secondary-cursor{width:60px;height:60px}.secondary-cursor .cursor-background{width:100%;height:100%;border-radius:50%;border:2px solid #f19393;position:relative}.secondary-cursor .cursor-background:after,.secondary-cursor .cursor-background:before{content:"";width:3px;height:3px;border-radius:50%;background:#fff;display:block;position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);-webkit-animation:fadeOut .75s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:fadeOut .75s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards}.secondary-cursor .cursor-background:before{left:10px}.secondary-cursor .cursor-background:after{right:10px}.slider-drag .cursor-background{-webkit-animation:scaleUp 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards,bgUp 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:scaleUp 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards,bgUp 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards}.slider-drag .cursor-background:after{-webkit-animation:fadeIn 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards,translateRightDot 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:fadeIn 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards,translateRightDot 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards}.slider-drag .cursor-background:before{-webkit-animation:fadeIn 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards,translateLeftDot 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:fadeIn 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards,translateLeftDot 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards}.slider-drag .main-cursor{-webkit-animation:fadeOut .75s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:fadeOut .75s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards}.hamburger .cursor-background{-webkit-animation:fadeOut 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:fadeOut 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards}.hamburger .main-cursor-background{-webkit-animation:scaleUp 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:scaleUp 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes fadeOut{0%{opacity:1}100%{opacity:0}}@keyframes fadeOut{0%{opacity:1}100%{opacity:0}}@-webkit-keyframes bgUp{from{background-color:transparent}to{background-color:#000}}@keyframes bgUp{from{background-color:transparent}to{background-color:#000}}@-webkit-keyframes bgDown{from{background-color:#000}to{background-color:transparent}}@keyframes bgDown{from{background-color:#000}to{background-color:transparent}}@-webkit-keyframes scaleUp{from{-webkit-transform:scale(1);transform:scale(1)}to{-webkit-transform:scale(1.5);transform:scale(1.5)}}@keyframes scaleUp{from{-webkit-transform:scale(1);transform:scale(1)}to{-webkit-transform:scale(1.5);transform:scale(1.5)}}@-webkit-keyframes scaleDown{from{-webkit-transform:scale(1.5);transform:scale(1.5)}to{-webkit-transform:scale(1);transform:scale(1)}}@keyframes scaleDown{from{-webkit-transform:scale(1.5);transform:scale(1.5)}to{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes translateLeftDot{from{-webkit-transform:translate(20px, -50%);transform:translate(20px, -50%)}to{-webkit-transform:translate(0px, -50%);transform:translate(0px, -50%)}}@keyframes translateLeftDot{from{-webkit-transform:translate(20px, -50%);transform:translate(20px, -50%)}to{-webkit-transform:translate(0px, -50%);transform:translate(0px, -50%)}}@-webkit-keyframes translateRightDot{from{-webkit-transform:translate(-20px, -50%);transform:translate(-20px, -50%)}to{-webkit-transform:translate(0px, -50%);transform:translate(0px, -50%)}}@keyframes translateRightDot{from{-webkit-transform:translate(-20px, -50%);transform:translate(-20px, -50%)}to{-webkit-transform:translate(0px, -50%);transform:translate(0px, -50%)}}
.video-slider{width:100%;height:100%}.video-slider video{position:absolute;height:23vw;margin:2vh;border-radius:2%;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);opacity:0}
.image-slider{opacity:1;position:absolute;top:0;display:flex;flex-direction:column}.image-slider img{height:23vw;margin:2vh;border-radius:2%}
.artwork-container{width:100%;height:100vh;display:flex;align-items:center;justify-content:center}.artwork-container.absolute-container{position:absolute;width:100%;height:100vh;top:0;left:0}.artwork-infos{height:100%;margin-right:7vw;flex:2 1;display:flex;flex-direction:column}.artwork-name{font-size:5vw;margin-top:15vh;font-family:"Nanum Gothic Coding";height:20vh}.artwork-name>div>div>div{opacity:0}.artwork-desc{opacity:0;margin-top:5vh;font-size:1.5vw;text-align:justify}.artwork-button{--buton-color: pink;width:100%;padding:10px 0;background-color:transparent;border:3px solid var(--buton-color);margin-top:5vh;text-align:center;font-family:"Nanum Gothic Coding";text-transform:capitalize;transition:ease-out .4s}.artwork-button:hover{box-shadow:inset 0 100px 0 0 var(--buton-color);color:#221f1f;letter-spacing:3px}.artwork-pictures{position:relative;min-height:100%;flex:3 1}

.project-container{width:100%;height:100vh;display:flex;align-items:center;color:#000;font-size:min(1vw,1em)}.project-container.absolute-container{position:absolute;top:0;left:0}.project-infos{width:50%;height:100%;padding:0 7em;display:flex;flex-direction:column;justify-content:center;-webkit-perspective:100rem;perspective:100rem}.project-infos>*{-webkit-transform-origin:left;transform-origin:left}.project-media{width:50%;height:100%;position:relative}.project-name{font-size:3em;font-family:"Nanum Gothic Coding"}.project-desc{margin:5vh 0;font-size:1.5em;text-align:justify}.project-button{--buton-color: black;display:flex;align-items:center;justify-content:center;width:100%;padding:10px 0;height:max(40px,8vh);background-color:transparent;border:3px solid var(--buton-color);margin:.5em 0;text-align:center;font-family:"Nanum Gothic Coding";font-size:1.75em;text-transform:capitalize;transition:ease-out .2s}.project-button:hover{box-shadow:inset 0 100px 0 0 var(--buton-color);color:#dbc5c5;letter-spacing:3px}.project-image{width:50%;position:absolute}
.header{position:fixed;top:0;width:100%;display:flex;justify-content:space-between;mix-blend-mode:difference}.header-name{margin-left:2vw;margin-top:2vh;font-size:2.2vw;font-family:"Unica One",cursive}.header-icons{margin-top:5vh;margin-right:5vw}.header-icons svg{margin:0 10px;transition:.15s}.header-icons svg:hover{-webkit-transform:scale(1.25);transform:scale(1.25)}
.bg{position:fixed;top:0;left:0;width:100%;height:100vh;margin:0;padding:0;background-color:#000}
